
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  animation: fadeIn 0.15s ease-in-out forwards;
  background: rgba(255, 255, 255, 0.95);
}
.Modal__dialog {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  padding: 1rem;
}
.Modal.is-closing {
  animation: fadeOut 0.15s ease-in-out forwards;
}
.Modal__close {
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  position: fixed;
  background: rgba(255, 255, 255, 0.9);
}
.Modal__closeButton {
  position: fixed;
  right: 0;
  top: 0;
  height: 4rem;
  width: 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}
@keyframes fadeIn{
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeOut{
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}



