
.Alert {
  padding: 1rem;
  background: #169a3e;
  color: #fff;
  font-weight: 500;
  font-weight: 300;
}
.Alert .container {
  text-align: justify;
}
.Alert__title {
  font-weight: 500;
  line-height: 1.6;
}
.Alert strong {
  font-weight: 500;
}
.CompanieList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 24px;
}
.CompanieList__item {
  transition: all 0.3s;
  will-change: transform;
  text-decoration: none;
  margin: 6px;
  cursor: pointer;
}
.CompanieList__item:hover {
  /*transform: scale(1.1);*/
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.1);
  z-index: 2;
}
.CompanieList__imageBox {
  width: 300px;
  height: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.CompanieList__info {
  background: #ffffff;
  padding: 1rem;
  color: #333;
  border-top: 1px solid #f1f1f1;
  max-width: 300px;
}
.CompanieList__name,
.CompanieList__description {
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.CompanieList__name {
  font-weight: 600;
}
.CompanieList__description {
  font-weight: 300;
  margin-top: 6px;
  color: #666;
}
.CompanieList__image {
  max-width: 200px;
  max-height: 140px;
}

/*Modal*/
.Companie {
  text-align: center;
}
.Companie__image img {
  max-height: 180px;
}
.Companie__image {
  max-width: 240px;
  width: 100%;
  display: block;
  margin: 0 auto;
}
.Companie__name {
  font-weight: 800;
  font-size: 1.8rem;
  margin-top: 2rem;
  color: #333;
  display: inline-block;
}
.Companie__description {
  margin-top: 2rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.45;
}
.Link__icon {
  display: inline;
  width: 24px;
  height: 24px;
  fill: rgba(0, 0, 0, 0.6);
  margin-top: -10px;
  margin-left: 6px;
}
.Alert.is-open > .Alert__icon {
  transform: rotate(180deg)translateY(10px);
}
.Alert__icon {
  width: 48px;
  height: 48px;
  cursor: pointer;
  fill: rgba(255, 255, 255, 1);
}
@media screen and (min-width: 768px) {
.Companie__name {
    font-size: 2.4rem;
}
.Companie__description {
    font-size: 1.4rem;
}
}



